<script>
import topPic from '@/assets/home/top.jpg';
import topPic2 from '@/assets/home/top2.jpg';
import topPic3 from '@/assets/home/top3.jpg';
import topPic4 from '@/assets/home/top4.jpg';

let timer;

export default {
  name: 'stu_header',
  data() {
    return {
      showSubIndex: 0,
      showProgress: true,
      bannerIndex: 0,
      count: 0,
      currentCount: 0,
      swiperList: [
        {
          title: '南科大未来网络研究院',
          subTitle: '建设世界一流的网络学科',
          pic: topPic
        },
        {
          title: '2018年04月07日',
          subTitle: '南海立体通信网络高峰论坛',
          pic: topPic2
        },
        {
          title: '南科大未来网络研究院',
          subTitle: '建设世界一流的网络学科',
          pic: topPic3
        },
        {
          title: '2018年04月07日',
          subTitle: '南海立体通信网络高峰论坛',
          pic: topPic4
        }
      ],
      desList: [
        {
          id: '01',
          title: '研究院介绍'
        },
        {
          id: '02',
          title: '新闻介绍'
        },
        {
          id: '03',
          title: '成果展示'
        },
        {
          id: '04',
          title: '团队建设'
        },
      ]
    };
  },
  mounted() {
    // timer = setInterval(() => {
    //   console.log(this.progressWidth)
    //   if (this.progressWidth < 100) {
    //     this.progressWidth += 1;
    //   } else {
    //     this.progressWidth = 0
    //   }
    // }, 5);
    
  },
  methods: {
    changeBanner(newIndex, oldIndex) {
      //console.log(newIndex, oldIndex);
      this.bannerIndex = newIndex;
      this.showProgress = false;
      setTimeout(() => {
        this.showProgress = true;
      }, 10);
    },
    showSubTitle(e) {
      let id = e.currentTarget.dataset.subindex;
      this.showSubIndex = id;
    },
    mouseOnItem(){
      clearTimeout(timer);
    },
    closeSubTitle() {
      timer = setTimeout(() => {
        console.log('debounce exe');
        this.showSubIndex = 0;
      }, 200);
    },
    debounce(fn, delay) {
      console.log('debounce');
      var delay = delay || 200;
      var timer;
      return function () {
          var th = this;
          var args = arguments;
          if (timer) {
              clearTimeout(timer);
          }
          timer = setTimeout(function () {
              timer = null;
              fn.apply(th, args);
          }, delay);
      };
    }
  },
}
</script>

