import Vue from 'vue';
import Router from 'vue-router';
import Home from '../views/Home/Home.vue';

import NewsCenter from './newsCenter';

Vue.use(Router);

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home,
  },
  {
    path: '/app',
    name: 'app',
    component: () => import(/* webpackChunkName: "about" */ '../App.vue'),
  },
  ...NewsCenter
]

export default new Router({ routes:routes });
